import React,{useState,useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import {Star,StarBorder} from '@material-ui/icons';

function FavSetting(props){

  let [tab, setTab] = useState(null);
  let [favs, setFavs] = useState(props.favs);

  useEffect(()=>setFavs(props.favs), [props.favs, props.open]);

  let styles = useStyles();

  let handleSave = _ => {
    localStorage.setItem(props.favType, JSON.stringify(favs));
    props.updateFavs();
    handleClose();
  };

  let handleClose = _ => {
    props.setOpen(false);
    setFavs({});
    setTab(null);
  };

  let groups = {};
  Object.keys(props.favChoices||{}).forEach(id=>groups[props.favChoices[id].type]=true);

  return (
    <Dialog open={props.open} onClose={handleClose} fullWidth>
      <DialogTitle>Favorite {props.favTypeText}</DialogTitle>
      <DialogContent>
        <Tabs
          TabIndicatorProps={{style:{backgroundColor:'#0000'}}}
          value={tab}
          textColor="secondary"
          onChange={(e,val)=>setTab(val)}
          className={styles.tab}
        >
          <Tab value={null} label="All" wrapped={true} />
          {Object.keys(groups).map(group=><Tab key={group} value={group} label={group} />)}
        </Tabs>
        <Paper className={styles.list}>
          {
            Object.keys(props.favChoices||{})
              .filter(id=>tab==null||props.favChoices[id].type===tab)
              .sort((ida,idb)=>(props.favChoices[ida].fullname||props.favChoices[ida].name)
                .localeCompare(props.favChoices[idb].fullname||props.favChoices[idb].name))
              .map(id=> <Paper className={`${styles.item} ${favs[id]?styles.selectedItem:''}`}
                               onClick={()=>setFavs({...favs, [id]:!favs[id]})} key={id}
                        >
                          {favs[id] ? <Star color='secondary' className={styles.star}/>:
                                      <StarBorder color='action' className={styles.star}/>}
                          {props.favChoices[id].fullname || props.favChoices[id].name}
                        </Paper>)
          }
        </Paper>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button onClick={handleSave} color="primary">
          Save
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default FavSetting;

let useStyles = makeStyles({
  tab:{
    backgroundColor: 'whitesmoke',
    borderRadius: '10px 10px 0px 0px',
    '& [role=tablist]':{
      flexFlow:'wrap'
    },
    '& [role=tablist]>.Mui-selected':{
      backgroundColor:'#f7d7e1',
      borderRadius: '10px 10px 0px 0px',
      border: '2px #f7d7e6 solid'
    },
    '& [role=tablist]>button':{
      backgroundColor:'#00000008',
      borderRadius: '10px 10px 0px 0px',
      borderTop: '2px #f5f5f5 solid'
    }
  },
  list:{
    height: '50vh',
    overflow: 'auto',
    backgroundColor: '#f5f5f5',
    position: 'relative'
  },
  item:{
    padding: 8,
    margin: 5,
    paddingLeft: 35,
    userSelect: 'none',
    '&:hover':{
      transform: 'scale(1.02)'
    }
  },
  selectedItem:{
    backgroundColor: '#f66497',
    fontWeight: 'bold'
  },
  star:{
    position: 'absolute',
    left: 10
  }
});