import React, {useState,useReducer,useEffect} from 'react';
import $ from 'jquery';

let reqId = 0;
let globalForceUpdateFFClass;

export function useClubType(props){
  let [clubs, setClubs] = useState({});
  useEffect(() => {
    async function fetchClubType(){
      var res = await $.ajax({
        type: "GET",
        url: "https://api-mobile.circuithq.com/api/v2/club/sg",
        dataType: 'json',
        headers: {
          "Authorization": props.accessToken,
          "User-Country-Code": "sg"
        }
      });
      setClubs(res.data.reduce((result,item)=>{
        result[item.clubId] = {
          name: item.name,
          address: item.address,
          fullname: item.clubWebsiteName,
          shortname: item.name.replace(/^FFP?\s*/,''),
          type: item.name.indexOf('FFP') >= 0 ? 'Platinum': 'Premium'
        };
        return result;
      },{}));
    }
    fetchClubType();
  }, [props.accessToken]);
  return clubs;
}

export function useClassType(props){
  let [classes, setClasses] = useState({});
  useEffect(() => {
    async function fetchClassType(){
      var res = await $.ajax({
        type: "GET",
        url: "https://api-mobile.circuithq.com/api/v2/topic/sg",
        dataType: 'json',
        headers: {
          "Authorization": props.accessToken,
          "User-Country-Code": "sg"
        }
      });
      let groupTypes = {};
      setClasses(res.data.flatMap(group=>{
        groupTypes[group.programTypeId] = group.name;
        return group.subTopics;
      }).reduce((result,item)=>{
        result[item.programId] = {
          name: item.name,
          shortname: item.name.replace(/([A-Za-z0-9])([A-Za-z0-9]*)/g,(a,b,c)=>b.toUpperCase()+c.toLowerCase()),
          type: groupTypes[item.programTypeId]
        };
        return result;
      },{}));
    }
    fetchClassType();
  }, [props.accessToken]);
  return classes;
}

export async function searchClass(props, page=1){
  var data = {
    programIds: props.paramClasses,
    fromDate: props.fromDate ? props.fromDate : parseInt(Date.now()/1000 + props.fromDateAdj),
    toDate: props.toDate ? props.toDate : parseInt(Date.now()/1000 + props.toDateAdj),
    pageSize: 100,
    pageNumber: page,
    distance: 2147483647,
    latitude: 0,
    longitude: 0
  };
  if(props?.paramClub !== 'FFP'){
    data.clubId = props.paramClub;
  }
  console.log("Search Start");
  console.log(data);
  var res = await $.ajax({
    type: "GET",
    url: "https://api-mobile.circuithq.com/api/v2/class/search",
    dataType: 'json',
    headers: {
      "Authorization": props.accessToken,
      "User-Country-Code": "sg"
    },
    data: data
  });
  function toTime(timestamp){
    var d = new Date(timestamp*1000);
    return d.toTimeString().substring(0,5);
  }
  function toDate(timestamp){
      var d = new Date(timestamp*1000);
      return d.toLocaleDateString();
  }
  function toDay(timestamp){
      var d = new Date(timestamp*1000);
      return ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'][d.getDay()];
  }
  var classes = res.data;
  classes = classes.map( c => {
      return {
          classId:c.classId,
          class:c.name,
          club:props.clubTypes[c.club.clubId]?.fullname,
          clubTier:props.clubTypes[c.club.clubId]?.type,
          clubId:c.club.clubId,
          date:toDate(c.timeStart),
          day:toDay(c.timeStart),
          timestamp:c.timeStart,
          duration:c.timeEnd-c.timeStart,
          start:toTime(c.timeStart),
          end:toTime(c.timeEnd),
          capacity:c.capacity,
          address:c.club.address
      }
  }).filter( c => c.capacity > 0 || !props.accessToken || props.paramShowFull );
  if(props.paramClub === 'FFP') classes = classes.filter(c=>c.clubTier==='Platinum');
  if(props.paramTime === 'No Work') classes = classes.filter(c=>c.day==='Sat'||c.day==='Sun'||parseInt(c.end)<=11||parseInt(c.start)>=20);
  return classes;
}

export function useFFClass(props){
  let [classes, setClasses] = useState([]);
  let [updateFFClassCnt, forceUpdateFFClass] = useReducer(x=>x+1 ,0);
  globalForceUpdateFFClass = forceUpdateFFClass;

  useEffect(function(){
    if(props.paramClasses && Object.keys(props.clubTypes).length && Object.keys(props.classTypes).length){
      (async (currReqId) => {
        setClasses([]);
        let classes = await searchClass(props);
        if(currReqId === reqId){
          setClasses(classes);
          if(classes.length < 30){
            let classes2 = await searchClass(props,2);
            if(currReqId === reqId){
              setClasses([...classes, ...classes2]);
            }
          }
        }
      })(++reqId);
    }
  }, [
      props.fromDateAdj, props.toDateAdj,
      props.paramClub, props.paramClasses, props.paramTime,
      props.accessToken, updateFFClassCnt, props.clubTypes, props.classTypes
  ]);

  return classes;
}

export function forceUpdateFFClass(){
  globalForceUpdateFFClass();
}

function getExactTime(date){
  return date.getHours()+':'+date.getMinutes()+':'+date.getSeconds()+'.'+date.getMilliseconds()
}

async function bookClass(classId, accessToken, debug, setExecuting){

  console.log('Booking... ' + classId + ' ('+ getExactTime(new Date()) + ')');
  let beforeTime = new Date().getTime();

  try {

    if(debug){
      console.log('DEBUG');
      let r1 = Math.random()*Math.random()*Math.random();
      let r2 = Math.random()*Math.random()*Math.random();
      await new Promise(r => setTimeout(r, 50 + r1*r2*20000));
      if(Math.random() > 0.01)
        throw {responseText:'{"error":{"messages":["Debug_Error"]}}'};
    }else{
      var res = await $.ajax({
        type: "POST",
        url: "https://api-mobile.circuithq.com/api/v2/class/book",
        dataType: 'json',
        headers: {
          "Authorization": accessToken,
          "User-Country-Code": "sg"
        },
        data: {
          class_id: classId
        }
      });
      console.table(res);
      setExecuting(null);
    }

    let afterTime = new Date().getTime();
    console.log('[' + (afterTime-beforeTime) + 'ms] Success... ' + classId + ' ('+ getExactTime(new Date()) + ')');
    return true;
  } catch (err) {
    let res = JSON.parse(err.responseText);
    if(res && res.message)
      console.error(res.message); // booking_errors_too_soon booking_errors_overlap errors_unexpected errors_maintenance_mode
    else if(res && res.error && res.error.messages)
      res.error.messages.forEach(x=>console.error(x?.message));
    else
      console.error(res);
    let afterTime = new Date().getTime();
    console.log('[' + (afterTime-beforeTime) + 'ms] Failed... ' + classId + ' ('+ getExactTime(new Date()) + ')');
    return false;
  }
}

export function bookClassTimeout(classId, accessToken, debug, timeout, setExecuting){
  let p1 = bookClass(classId, accessToken, debug, setExecuting).then( res => Promise.resolve(_=>new Promise(r=>r(res))) );
  let p2 = new Promise(r => setTimeout(r, timeout)).then( _ => _=>Promise.resolve(p1.then(r=>Promise.resolve(r()))) );
  return Promise.race([p1, p2]);
}