import React,{useState,useReducer,useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import IconButton from '@material-ui/core/IconButton';
import {Settings,Stars} from '@material-ui/icons';
import ClassSetting from './ClassSetting';
import FavSetting from './FavSetting';

import {forceUpdateFFClass} from './HttpRequest';

function ClassFilter(props){

  let [favClubs, setFavClubs] = useState({});
  let [favClasses, setFavClasses] = useState({});
  let [favs, setFavs] = useState({});
  let [favType, setFavType] = useState(null);
  let [favTypeText, setFavTypeText] = useState(null);
  let [favChoices, setFavChoices] = useState(null);

  let updateFavs = ()=>{
    setFavClubs(JSON.parse(localStorage.getItem('favClubs')||'{}'));
    setFavClasses(JSON.parse(localStorage.getItem('favClasses')||'{}'));
  };
  useEffect(updateFavs, []);

  let [favDialogOpen, setFavDialogOpen] = useState(false);
  let [settingDialogOpen, setSettingDialogOpen] = useState(false);

  let clubs = {
    'All': null,
    'Platinum': 'FFP',
    ...Object.keys(favClubs).filter(id=>favClubs[id]).reduce((result,id)=>{
      result[props.clubTypes[id]?.shortname] = id;
      return result;
    }, {})
  };
  let classes = {
    'Access': [183,184,185,182],
    ...Object.keys(favClasses).filter(id=>favClasses[id]).reduce((result,id)=>{
      result[props.classTypes[id]?.shortname] = [id];
      return result;
    }, {})
  };
  const times = ['All', 'No Work'];

  let [selectedClub, setSelectedClub] = useReducer((curr,sel)=>{
    localStorage.setItem('selClub', sel);
    return sel;
  }, null, ()=>localStorage.getItem('selClub'));
  let [selectedClasses, setSelectedClasses] = useReducer((curr,sel)=>{
    localStorage.setItem('selClasses', JSON.stringify(sel));
    return sel;
  }, {}, ()=>JSON.parse(localStorage.getItem('selClasses')||'{}'));
  let [selectedTime, setSelectedTime] = useReducer((curr,sel)=>{
    localStorage.setItem('selTime', sel);
    return sel;
  }, 'No Work', ()=>localStorage.getItem('selTime'));

  useEffect(function(){
    props.setParamClub(selectedClub);
    props.setParamClasses('['+Object.keys(selectedClasses).filter(key=>selectedClasses[key]).map(key=>classes[key]).flat()+']');
    props.setParamTime(selectedTime);
  });

  let styles = useStyles();

  return (
    <div style={blockStyle}>
      <Paper elevation={3} style={{padding:5}} className={styles.filter}>
        <span style={{float: 'right'}}>
          <IconButton onClick={_=>setSettingDialogOpen(true)}><Settings color='action' fontSize='small'/></IconButton>
          <ClassSetting open={settingDialogOpen} setOpen={setSettingDialogOpen}
          accessToken={props.accessToken} setAccessToken={props.setAccessToken}/>
          <FavSetting open={favDialogOpen} setOpen={setFavDialogOpen} {...{favs, favType, favTypeText, favChoices, updateFavs}} />
        </span>
        <strong>Club: </strong>
        <IconButton style={{position:'absolute', margin:-10}} onClick={_=>{
          setFavType('favClubs');setFavTypeText('Clubs');setFavChoices(props.clubTypes);setFavs(favClubs);setFavDialogOpen(true);
        }}><Stars color='action' fontSize='small'/></IconButton>
        <br/>
        {
          Object.keys(clubs).map(name=><span key={name}><FormControlLabel control={
            <Radio checked={selectedClub==clubs[name]}
            onChange={_=>setSelectedClub(clubs[name])}/>
          } label={name}/></span>)
        }<br/>
        <strong>Class: </strong>
        <IconButton style={{position:'absolute', margin:-10}} onClick={_=>{
          setFavType('favClasses');setFavTypeText('Classes');setFavChoices(props.classTypes);setFavs(favClasses);setFavDialogOpen(true);
        }}><Stars color='action' fontSize='small'/></IconButton>
        <br/>
        {
          Object.keys(classes).map(name=><span key={name}><FormControlLabel control={
            <Checkbox checked={selectedClasses[name] || false}
            onChange={_=>setSelectedClasses({...selectedClasses, [name]:!selectedClasses[name]})}/>
          } label={name}/></span>)
        }<br/>
        <strong>Time: </strong><br/>
        {
          times.map(name=><span key={name}><FormControlLabel control={
            <Radio checked={selectedTime==name}
            onChange={_=>setSelectedTime(name)}/>
          } label={name}/></span>)
        }<br/>
        <div style={buttonStyle}>
          <Button component={Paper} style={{backgroundColor:'#ff4571'}}
              onClick={forceUpdateFFClass}>Search</Button>
        </div>
      </Paper>
    </div>
  );
}

export default ClassFilter;

var blockStyle = {
  margin: '5px 0',
  flexGrow: 1,
  userSelect: 'none'
}

const useStyles = makeStyles({
  filter: {
    '&:hover': {
      backgroundColor: '#fffffff0'
    },
    transition: 'background-color 0.1s',
    backgroundColor: '#ffffffd0'
  }
});

var buttonStyle = {
  float: 'right',
  margin: '-40px 5px',
  gap: 5,
  display: 'grid',
  gridTemplateColumns: 'auto auto'
}
