import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import {useFFClass} from './HttpRequest';

function ClassTable(props){
  let classes = useFFClass(props);
  let styles = useStyles();

  let handleClick = (c) => {
    props.handleSelection(c);
  };

  let prevDate=null;

  return (
    <Paper elevation={3} className={styles.tableWrapper}>
      <TableContainer className={styles.tableContainer} style={{maxHeight: classes.length?'':36}}>
        <Table stickyHeader className={styles.table} size="small">
          <TableHead className={styles.thead}>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Club</TableCell>
              <TableCell>Class</TableCell>
              <TableCell>Date</TableCell>
              <TableCell>Time</TableCell>
              <TableCell>Capacity</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {classes.sort((a,b)=>a.timestamp-b.timestamp).map(c => {
              let currPrevDate = prevDate;
              prevDate = c.date;
              return <TableRow key={c.classId}
                onClick={() => handleClick(c)}
                style={{
                  backgroundColor:props.selected?.classId===c.classId?'#00d8f57d':'',
                  boxShadow:currPrevDate==null||c.date===currPrevDate?'':'#b6ccdd 0 2px 0 0 inset'
                }} hover>
                  <TableCell>{c.classId}</TableCell>
                  <TableCell>{c.club}</TableCell>
                  <TableCell>{c.class}</TableCell>
                  <TableCell>{c.date} ({c.day})</TableCell>
                  <TableCell>{c.start} - {c.end}</TableCell>
                  <TableCell>{c.capacity}</TableCell>
                </TableRow>;
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  );
}

const useStyles = makeStyles({
  tableWrapper: {
    maxWidth: 1000,
    margin: '5px 0',
    backgroundColor: '#ffffffe0'
  },
  tableContainer: {
    maxHeight: 'calc(100vh - 20px)',
    transition: 'max-height 0.5s'
  },
  table: {
    minWidth: 650,
    userSelect: 'none'
  },
  thead:{
    '& th': {
      fontWeight: 'bold',
      backgroundColor: '#b6ccdd'
    }
  }
});

export default ClassTable;
