import React,{useState,useReducer,useEffect,useContext} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import {EventNote,Map,VerifiedUser,Cancel,HelpOutline} from '@material-ui/icons';
import Tooltip from '@material-ui/core/Tooltip';
import {DebugContext} from './Context.js';

import {bookClassTimeout, searchClass} from './HttpRequest';

let globalExecuting = null;

function ClassExecutor(props){

  let [timestamp, setTimestamp] = useState(parseInt(Date.now()/1000));
  let [watches, dispatchWatches] = useReducer((watches, action) => {
    let output = {};
    if(action.action === 'ADD' || action.action === 'UPDATE'){
      output = {...watches, [action.target.classId]:action.target};
    }else if(action.action === 'REMOVE'){
      let clone = {...watches};
      delete clone[action.target.classId];
      output = clone;
    }else if(action.action === 'EXECUTE'){
      action.target.timestamp = timestamp + props.bookOpen + props.bookOffset + 3;
      output = {...watches, [action.target.classId]:action.target};
    }
    localStorage.setItem('watches', JSON.stringify(output));
    return output;
  }, {}, _=>{
    let stored = localStorage.getItem('watches');
    return stored ? JSON.parse(stored) : {};
  });
  let [executing, setExecuting] = useReducer((executing, newValue) => {
    globalExecuting = newValue;
    return newValue;
  }, null);
  let styles = useStyles();
  let debug = useContext(DebugContext);

  useEffect(function(){
    (async () => {
      let success = 0;
      let fail = 0;
      while(true){
        if(executing !== null && globalExecuting !== null){
          let bookPromise = await bookClassTimeout(executing, props.accessToken, debug, props.bookWait, setExecuting);
          console.log(bookPromise());
          bookPromise().then(canBook => {
            if(canBook){
              dispatchWatches({
                action:'UPDATE',
                target:{...watches[executing], success:++success, fail:fail}
              });
              setExecuting(null);
            }else{
              dispatchWatches({
                action:'UPDATE',
                target:{...watches[executing], success:success, fail:++fail}
              });
            }
          });
        }else{
          break;
        }
      }
    })();
  }, [executing]);

  useEffect(function(){
    setInterval(_=>{
      setTimestamp(parseInt(Date.now()/1000));
    }, 100);
  }, []);

  useEffect(function(){
    Object.keys(watches).forEach(async id=>{
      let c = watches[id];
      if(!c.lastSeen || timestamp-c.lastSeen >= 12*60*60){
        c.seenStatus = false;
        try{
          let result = await searchClass({
            fromDate: c.timestamp-15*60,
            toDate: c.timestamp+c.duration+15*60,
            paramClub: c.clubId,
            paramClasses: '[]',
            paramTime: 'All',
            paramShowFull: true,
            accessToken: props.accessToken
          });
          let target = result.filter(c => c.classId == id)[0];
          c.seenStatus = !!target;
          c.seenCapacity = target?.capacity;
        }catch(err){
          delete c.seenStatus;
          console.error(err);
        }finally{
          c.lastSeen = parseInt(Date.now()/1000);
          dispatchWatches({action: 'UPDATE', target: c});
        }
      }
    });
  }, [parseInt(timestamp/10)]);

  let toGoogleDate = d => d.toISOString().replace(/[-:]/g,'').replace(/\d\d\.\d\d\d/,'00');

  let saveCalendar = c => {
    c.calendarSaved = true;
    dispatchWatches({action: 'UPDATE', target: c});
    window.open('http://www.google.com/calendar/render?'+
`action=TEMPLATE
&text=${c.class}
&dates=${toGoogleDate(new Date(c.timestamp*1000))}/${toGoogleDate(new Date(c.timestamp*1000+(c.duration||1.5*60*60)*1000))}
&details=${c.club + ': ' + c.class}
&location=${escape(c.address)}
&trp=false
&sprop=
&sprop=name:`, '_blank');
  };

  let openMap = c => {
    window.open(`http://google.com/maps/dir//${escape(c.address)}`, '_blank');
  };

  let formatTime = (sec, short=false) => {
    var dd = parseInt(sec/60/60/24);
    if(short && dd > 0) return dd+'d';
    var hh = parseInt(sec/60/60%24);
    if(short && hh > 0) return hh+'h';
    var mm = parseInt(sec/60%60);
    if(short && mm > 0) return mm+'m';
    var ss = parseInt(sec%60);
    if(short && ss > 0) return ss+'s';
    var ddhhmmss = (dd?dd+'d ':'')+(hh?hh+'h ':'')+(mm?mm+'m ':'')+(ss?ss+'s ':'');
    return ddhhmmss;
  }

  let processBook = c => {
    var timeRemaining = c.timestamp - timestamp - props.bookOpen - props.bookOffset;
    let sresult = c.success?<span>SUCCESS: <strong style={{color:'green'}}>{c.success}</strong> </span>:'';
    let fresult = c.fail?<span>FAILED: <strong style={{color:'red'}}>{c.fail}</strong></span>:'';
    let result = (sresult||fresult)&&<div>{sresult}{fresult}</div>;
    if(timeRemaining <= 0 && timeRemaining+props.bookDuration >= 0 && executing == null && !result){
      setExecuting(c.classId);
    }
    if(executing === c.classId){
      timeRemaining += props.bookDuration;
      if(timeRemaining >= 0){
        return <strong><span style={{color:'red'}}>...EXECUTING...</span> (finish in {parseInt(timeRemaining)} s)<br/>{result}</strong>;
      }else{
        setExecuting(null);
      }
    }else if(timeRemaining >= 0){
      var color = {color: (timeRemaining>3*60*60)?'black':(timeRemaining>1*60*60?'orange':'red')};
      var ddhhmmss = formatTime(timeRemaining);
      return <span>
        Start Booking in <strong style={color}>{ddhhmmss}</strong>
      </span>;
    }else if(result){
      return <span>{result}</span>;
    }
    return <span>Invalid</span>
  };

  let preview = (
    <div style={blockStyle}>
      <Paper elevation={3} style={{
        backgroundColor:props.targetClass?(watches[props.targetClass.classId]?'#fdc4d2':'#c4f6fd'):'',
        padding:5
      }} className={styles.preview}>
        <div style={{...buttonStyle, margin:'auto'}}>
          {
            props.targetClass && <IconButton onClick={_=>openMap(props.targetClass)}><Map color='action' fontSize='small'/></IconButton>
          }
        </div>
        <strong>Class ID: </strong> {props.targetClass?.classId} <br/>
        <strong>Club: </strong> {props.targetClass?.club} ({props.targetClass?.clubId}) <br/>
        <strong>Class: </strong> {props.targetClass?.class} <br/>
        <strong>Date: </strong> {props.targetClass?.date} ({props.targetClass?.day}) <br/>
        <strong>Time: </strong> {props.targetClass?.start} - {props.targetClass?.end} <br/>
        <strong>Capacity: </strong> {props.targetClass?.capacity} <br/>
        <div style={buttonStyle}>
          {
            props.targetClass && !watches[props.targetClass.classId] && <Button component={Paper} style={{backgroundColor:'#1c94f3b5'}}
            onClick={() => dispatchWatches({action:'ADD',target:props.targetClass})}>Add</Button>
          }
          {
            debug && props.targetClass && watches[props.targetClass.classId] && <Button component={Paper} style={{backgroundColor:'#d21cf3'}}
            onClick={() => dispatchWatches({action:'EXECUTE',target:props.targetClass})}>Execute</Button>
          }
          {
            props.targetClass && watches[props.targetClass.classId] && <Button component={Paper} style={{backgroundColor:'#f31c68b5'}}
            onClick={() => dispatchWatches({action:'REMOVE',target:props.targetClass})}>Remove</Button>
          }
        </div>
      </Paper>
    </div>
  );

  let queue = (
    <div style={{...blockStyle, overflowX:'hidden', overflowY:'overlay'}}>
      <Paper elevation={3} style={{padding: '1px 5px 1px 5px', backgroundColor: '#ffffffd0'}}>
        <div style={{marginBottom:-6}}><strong>Execution Queue</strong></div>
        {
          Object.keys(watches).map(id =>
            <div key={id} style={innerBlockStyle}>
              <Paper elevation={2} className={styles.hoverPaper} onClick={_=>props.handleSelection(watches[id])} style={{
                backgroundColor:props.targetClass?.classId==id?'#99f3ff':'',
                padding:3
              }}>
                <div style={{...innerBlockTitleStyle,
                  backgroundColor: executing==id?'#d07dff':(
                    watches[id].success?'#7dff84':(
                      watches[id].fail?'#ff7daa':'#d0d0d0'
                  )),
                  boxShadow: props.targetClass?.classId==id?'#00c4ffbf 0 0 99px 0 inset':'',
                  margin: '-3px -3px 0',
                  padding: '0 3px'
                }}>
                  <strong style={{textDecoration: 'underline'}}>{watches[id].club}</strong><strong>: {watches[id].class}</strong>
                  <Tooltip title={(_=>{
                    let classId = watches[id].classId;
                    let status = watches[id].seenStatus === false ? '[INVALID]' : '';
                    let capacity = (watches[id].seenCapacity??'?') + ' / ' + watches[id].capacity;
                    let lastSeen = watches[id].lastSeen && '(' + formatTime(parseInt(Date.now()/1000)-watches[id].lastSeen, true)+' ago)' || '';
                    return `${status} Cap: ${capacity} ${lastSeen}`;
                  })()} placement='top' classes={{tooltip:styles.tooltip}} arrow>
                    <span onClick={() => {delete watches[id].lastSeen; delete watches[id].seenStatus; dispatchWatches({action:'UPDATE',target:watches[id]});}}>{
                      watches[id].seenStatus === true ? <VerifiedUser fontSize='small' style={{color:'green', marginBottom:-3}}/> : 
                      (watches[id].seenStatus === false ? <Cancel fontSize='small' style={{color:'red', marginBottom:-3}}/> :
                        <HelpOutline fontSize='small' style={{color:'gray', marginBottom:-3}}/>)
                    }</span>
                  </Tooltip>
                  <span style={{color:'gray'}}></span>
                </div>
                <strong>{watches[id].date} ({watches[id].day})</strong><span> ({watches[id].start} - {watches[id].end})</span><br/>
                {processBook(watches[id])}
                <div style={buttonStyle}>
                  {
                    !!watches[id].success && <IconButton onClick={_=>saveCalendar(watches[id])}><EventNote color={watches[id].calendarSaved?'disabled':undefined} fontSize='small'/></IconButton>
                  }
                </div>
              </Paper>
            </div>)
        }
      </Paper>
    </div>
  );

  return (
    <React.Fragment>
      {preview}
      {!!Object.keys(watches).length && queue}
    </React.Fragment>
  );
}

export default ClassExecutor;

const useStyles = makeStyles({
  hoverPaper: {
    '&:hover': {
      backgroundColor: '#ffffffff',
      transform: 'scale(1.03)'
    },
    transition: 'transform 0.1s',
    backgroundColor: '#ffffff80'
  },
  preview: {
    '&:hover': {
      backgroundColor: '#fffffff0',
      transition: 'background-color 0.1s'
    },
    transition: '',
    backgroundColor: '#ffffffd0'
  },
  tooltip: {
    top: '8px',
    color: '#efefef',
    fontSize: 'small',
    backgroundColor: '#333333',
    '&>span.MuiTooltop-arrow':{
      backgroundColor: '#333333'
    }
  }
});

var blockStyle = {
  margin: '5px 0',
  flexGrow: 1,
  userSelect: 'none'
}

var innerBlockStyle = {
  margin: '8px 0px',
  flexGrow: 1
}

var innerBlockTitleStyle = {
  borderRadius: '5px 5px 0px 0px '
}

var buttonStyle = {
  float: 'right',
  margin: '-40px 5px',
  gap: 5,
  display: 'grid',
  gridTemplateColumns: 'auto auto'
}
